<template>
    <div>
<!--        <el-button type="primary">充值账户余额</el-button>-->

        <el-button type="primary" @click="dialogFormVisible_1 = true">充值余额</el-button>
        <el-button type="primary" @click="dialogFormVisible_2 = true">提现余额</el-button>

        <el-dialog title="充值余额" :visible.sync="dialogFormVisible_1">
            <el-form :model="form">
                <el-form-item label="充值金额" >
                    <el-input v-model="form.value" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="用户密码" >
                    <el-input v-model="form.password" autocomplete="off" type="password"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible_1 = false">取 消</el-button>
                <el-button type="primary" @click="charge">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="提现余额" :visible.sync="dialogFormVisible_2">
            <el-form :model="form">
                <el-form-item label="提现金额" >
                    <el-input v-model="form.value" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="用户密码" >
                    <el-input v-model="form.password" autocomplete="off" type="password"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible_2 = false">取 消</el-button>
                <el-button type="primary" @click="withdraw">确 定</el-button>
            </div>
        </el-dialog>

        <el-table
                :data="entities"
                stripe
                style="width: 100%">
            <el-table-column
                    prop="entity_id"
                    label="ID"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="entity_name"
                    label="实体名称"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="currency_id"
                    label="货币代号"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="currency_amount"
                    label="账户余额"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="currency_yesterday_stm"
                    label="昨日清算结果"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="currency_used"
                    label="今日预估结果"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="public_key"
                    label="公钥">
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "Entity",
        data() {
            return {
                msg: 'Hello Vue',
                entities: [],
                dialogFormVisible_1: false,
                dialogFormVisible_2: false,
                form: {
                    value: '',
                    password: ''
                }
            }
        },
        created() {
            const _this = this
            axios.get('/getEntityById').then(function (resp) {
                _this.entities = resp.data
                // console.log(resp)
            })
        },
        methods: {
            charge() {
                console.log(this.form)
                this.dialogFormVisible_1 = false
                let _this = this
                let data = {
                        value: this.form.value,
                        password: this.form.password
                }
                axios.post("/entityCharge", data).then(function (resp) {
                    _this.chargeSuccess(resp.data);

                })
            },
            withdraw() {
                console.log(this.form)
                this.dialogFormVisible_2 = false
                let _this = this
                let data = {
                    value: this.form.value,
                    password: this.form.password
                }
                axios.post("/entityWithdraw", data).then(function (resp) {
                    _this.chargeSuccess(resp.data);
                })
            },
            chargeSuccess(response) {
                if(response=='余额不足'){
                    this.$message({
                        message: response,
                        type: 'error'
                    });
                }else{
                    this.$message({
                        message: response,
                        type: 'success'
                    });
                }

            }
        }
    }
</script>

